html {
  font-size: 52.5%;
}
@media (min-width: 768px) {
  html {
    font-size: 62.5%;
  }
}

html,
body {
  height: 100%;
  font-family: "Inter", sans-serif;
}

body {
  font-size: 1.6em;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
}

.logo {
  width: 100%;
  max-width: 8rem;
}
@media (min-width: 1280px) {
  .logo {
    max-width: 12rem;
  }
}

.hide {
  display: none;
}

.space {
  margin: 1rem 0;
}

a {
  text-decoration: none;
}

textarea {
  resize: none;
}
textarea.desc {
  height: 15rem;
}

.zindex {
  z-index: 100000;
}

table {
  width: 100%;
}
table thead {
  display: none;
}
table tbody {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  font-size: 1.2em;
  color: #0b416f;
}
table tr {
  position: relative;
  display: flex;
  gap: 0.3rem;
  flex-direction: column;
  width: 100%;
  background: #e0effe;
  padding: 2rem;
}
table tr.row__hide {
  display: none;
}
table tr.row__view {
  display: flex;
}
table td {
  text-align: right;
  font-size: 0.8em;
}
table td::before {
  content: attr(data-label);
  float: left;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 1.2em;
}
table td.action__container {
  display: flex;
  justify-content: space-between;
  padding-top: 1rem;
}
table td.action__container::before {
  content: none;
}
@media (min-width: 768px) {
  table {
    width: 100%;
    text-align: center;
    border-collapse: separate;
    border-spacing: 0.5rem;
  }
  table thead {
    background-color: #0c91eb;
    display: table-header-group;
  }
  table th {
    background-color: #0c91eb;
  }
  table tbody {
    display: table-row-group;
  }
  table tr {
    position: relative;
    display: table-row;
  }
  table tr.row__hide {
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }
  table tr.row__view {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
  table th {
    padding: 2rem;
    color: #FFFFFF;
    font-size: 1.2em;
  }
  table td {
    background-color: #e0effe;
    padding: 1.5rem;
    color: #0b416f;
    text-align: center;
    font-size: 1em;
    border: 1px solid #7cc7fd;
  }
  table td.ellipsis {
    max-width: 20rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  table td::before {
    content: none;
  }
  table td.action__container {
    display: block;
  }
}

[data-label=Name] {
  text-transform: capitalize;
}

.total {
  display: flex;
  justify-content: space-between;
  padding: 2rem 1rem;
  font-weight: 700;
  color: #0b416f;
}
.total__num {
  color: #015aa3;
}

.alertas {
  position: absolute;
  right: 0;
  bottom: 0.1rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.alertas__container {
  display: flex;
  align-items: center;
  column-gap: 1rem;
  width: 35rem;
  border-radius: 1rem;
  color: #e0effe;
  padding: 1rem 2rem;
}
.alertas__container.exito {
  background: rgb(4, 71, 58);
  background: -moz-linear-gradient(90deg, rgb(4, 71, 58) 0%, rgb(34, 64, 73) 50%, rgb(34, 37, 47) 100%);
  background: -webkit-linear-gradient(90deg, rgb(4, 71, 58) 0%, rgb(34, 64, 73) 50%, rgb(34, 37, 47) 100%);
  background: linear-gradient(90deg, rgb(4, 71, 58) 0%, rgb(34, 64, 73) 50%, rgb(34, 37, 47) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#04473a", endColorstr="#22252f", GradientType=1);
}
.alertas__container.error {
  background: rgb(99, 0, 0);
  background: -moz-linear-gradient(90deg, rgb(99, 0, 0) 0%, rgb(66, 20, 41) 50%, rgb(34, 37, 47) 100%);
  background: -webkit-linear-gradient(90deg, rgb(99, 0, 0) 0%, rgb(66, 20, 41) 50%, rgb(34, 37, 47) 100%);
  background: linear-gradient(90deg, rgb(99, 0, 0) 0%, rgb(66, 20, 41) 50%, rgb(34, 37, 47) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#630000", endColorstr="#22252f", GradientType=1);
}
.alertas__message {
  font-size: 1.2em;
}
.alertas__icon {
  font-size: 2em;
}

.boton {
  background-color: #b9e0fe;
  padding: 15px 4rem;
  color: #0b416f;
  font-size: 1em;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5em;
}
.boton:hover {
  background-color: #7cc7fd;
  cursor: pointer;
}
.boton.disable {
  background-color: #b9e0fe;
  padding: 15px 4rem;
  color: #064d86;
  font-size: 1em;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5em;
}
.boton.disable:hover {
  background-color: "";
  cursor: pointer;
}
.boton.disable:hover {
  cursor: not-allowed;
}
.boton.xl {
  font-size: 2em;
}
.boton--inline {
  width: auto;
  display: inline-block;
}
.boton.danger {
  background-color: #eb06a1;
}
.boton--secundary {
  background-color: #0071c7;
  color: #FFFFFF;
}
.boton--secundary:hover {
  background-color: #008efa;
}
.boton--action {
  background-color: #b9e0fe;
  padding: 15px 4rem;
  color: #0b416f;
  font-size: 1em;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5em;
  width: auto;
  display: inline-block;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
}
.boton--action:hover {
  background-color: #7cc7fd;
  cursor: pointer;
}
.boton--action.delete {
  background-color: rgb(169, 0, 0);
}
.boton--action.update {
  background-color: rgb(0, 120, 0);
}
.boton--action.view {
  background-color: #0c91eb;
}

.form__login {
  width: 100%;
  max-width: 60rem;
}
.form__login .input {
  width: 100%;
  margin-bottom: 3rem;
  position: relative;
}
.form__login .input input {
  width: 100%;
  border: none;
  padding: 2.5rem;
  border-radius: 1.5rem;
  box-shadow: 2px 6px 32px -11px #0c91eb;
  -webkit-box-shadow: 2px 6px 32px -11px #0c91eb;
  -moz-box-shadow: 2px 6px 32px -11px #0c91eb;
}
.form__login .input input::placeholder {
  font-size: 2.5rem;
}
.form__login .input input:focus {
  border: 0.2rem solid #064d86;
  outline: none;
}
.form__login .input span {
  font-size: 3rem;
  position: absolute;
  right: 1rem;
  top: 30%;
  color: #015aa3;
}

#container {
  position: relative;
}

.modal {
  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000000;
}
.modal__input__invoice {
  border: 1px solid #064d86;
  max-width: 7rem;
  text-align: center;
  font-weight: 700;
}
.modal.active {
  display: flex;
}
.modal__content {
  position: relative;
}
.modal__content form {
  background-color: white;
}
.modal__icon {
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 1rem;
  font-size: 5rem;
  filter: invert(100%);
}
.modal__icon:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .modal__content {
    min-width: 50%;
  }
}

.container {
  display: flex;
  height: 100%;
  flex-direction: column;
}
@media (min-width: 1280px) {
  .container {
    flex-direction: row;
  }
}

.container__content {
  height: 100%;
}

.sidebar {
  z-index: 1000;
  background-color: #0b416f;
  width: 100%;
  height: 100%;
  animation: active 1s forwards;
  display: none;
}
.sidebar__title {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 2rem 0;
}
.sidebar__links {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 3rem;
}
.sidebar__link {
  display: flex;
  align-items: center;
  gap: 3rem;
  background-color: #b9e0fe;
  padding: 1rem;
  border-radius: 1rem;
  text-decoration: none;
  transition: all 0.3s ease;
}
.sidebar__link:hover {
  background-color: #cccccc;
}
.sidebar__link:hover .sidebar__link__title,
.sidebar__link:hover .sidebar__icon {
  color: #064d86;
}
.sidebar__link.selected {
  background-color: #FFFFFF;
}
.sidebar__link.selected .sidebar__link__title,
.sidebar__link.selected .sidebar__icon {
  color: #064d86;
}
.sidebar__icon {
  display: flex;
  color: #064d86;
}
.sidebar__icon span {
  font-size: 3rem;
}
.sidebar__link__title {
  color: #064d86;
}
.sidebar__close {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 3rem;
}
.sidebar__close__link {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.sidebar__close span {
  color: #FFFFFF;
}
.sidebar.active {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
@media (min-width: 1280px) {
  .sidebar {
    max-width: 30rem;
    display: block;
  }
  .sidebar__image {
    max-width: 12rem;
  }
}

.content {
  flex: 1;
  overflow: auto;
}

.close__icon {
  padding: 3rem;
  color: #b9e0fe;
  text-align: right;
  cursor: pointer;
}
.close__icon span {
  font-size: 5rem;
}
@media (min-width: 1280px) {
  .close__icon {
    display: none;
  }
}

.header--movile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem;
  background-color: #064d86;
}
.header--movile .material-symbols-outlined {
  color: #e0effe;
  font-size: 6rem;
}
.header--movile span {
  cursor: pointer;
}
@media (min-width: 1280px) {
  .header--movile {
    display: none;
  }
}

.content {
  padding: 2rem;
}

@keyframes active {
  0% {
    width: 50%;
  }
  100% {
    width: 100%;
  }
}
.login {
  display: grid;
  height: 100vh;
  grid-template-columns: 1fr;
  grid-template-rows: 200px 1fr;
}
.login__image {
  width: 100%;
  height: 100%;
  background-image: url("../img/loginBase.png");
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  flex: 1;
}
.login__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e0effe;
}
.login__content h1 {
  font-size: 5em;
  font-weight: 700;
  color: #0071c7;
}
@media (min-width: 480px) {
  .login {
    grid-template-columns: 1.5fr 2fr;
    grid-template-rows: 1fr;
    padding: 1rem;
  }
}

.statistics {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  padding: 1rem;
  background: #e0effe;
}
.statistics__row {
  display: grid;
  gap: 2rem;
}
.statistics__row > * {
  background-color: rgba(255, 255, 255, 0.6117647059);
  border-radius: 2rem;
  padding: 2rem;
}
.statistics__image__container img {
  max-width: 100%;
  width: 15rem;
}
.statistics__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.statistics__info__container h2,
.statistics__info__container p {
  text-align: center;
  color: #0071c7;
}
.statistics__info__container h2 {
  font-size: 4em;
}
.statistics__info__container p {
  font-size: 1.5em;
}
@media (min-width: 1280px) {
  .statistics {
    padding: 6rem;
    grid-template-rows: 1fr 1fr;
  }
  .statistics__row {
    grid-template-rows: 1fr;
  }
  .statistics__row.col2 {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .statistics__row.col3 {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.default__dashboard__actions__buttons {
  display: flex;
  justify-content: flex-end;
  column-gap: 2rem;
}
.default__dashboard__input {
  width: 100%;
  background-color: #e0effe;
  border: none;
  padding: 2rem 6rem;
  color: #0b416f;
}
.default__dashboard__input:focus {
  outline: 1px solid #7cc7fd;
}
.default__dashboard .search {
  position: relative;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  transition: all 0.2s ease-in;
}
.default__dashboard .search__icon {
  position: absolute;
  left: 2rem;
  top: 30%;
}
@media (min-width: 768px) {
  .default__dashboard {
    gap: 1rem;
  }
  .default__dashboard__title, .default__dashboard__desc {
    text-align: left;
  }
}

.search.active__search {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  padding: 1rem 0;
}

.search.hide__search {
  transition: none;
  visibility: hidden;
  padding: 1rem 0;
}

.views__header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1280px) {
  .views__header__container {
    flex-direction: row;
  }
}
.views__title, .views__desc {
  text-align: center;
  color: #015aa3;
}
.views__title {
  font-size: 3.5em;
  margin: 0;
}
.views__actions {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.views__actions__buttons {
  display: flex;
  justify-content: flex-end;
  column-gap: 2rem;
}
.views__input, .views__inputs {
  width: 100%;
  background-color: #e0effe;
  border: none;
  padding: 2rem 6rem;
  color: #0b416f;
}
.views__input:focus, .views__inputs:focus {
  outline: 1px solid #7cc7fd;
}
.views .search {
  position: relative;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  transition: all 0.3s ease-in-out;
}
.views .search__icon {
  position: absolute;
  left: 2rem;
  top: 30%;
}
@media (min-width: 1280px) {
  .views {
    gap: 1rem;
  }
  .views__title, .views__desc {
    text-align: left;
  }
}

.content__center {
  display: flex;
  align-items: center;
}

.form {
  width: 100%;
  box-shadow: 0px 10px 50px 12px rgba(0, 0, 0, 0.1);
  border-radius: 2rem;
  padding: 2rem 3rem;
}
.form__title {
  font-size: 3.5em;
  color: #064d86;
}
.form__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  margin: 1.5rem 0;
}
.form__row.top {
  margin-top: 5rem;
}
.form__row__item {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.form__label {
  color: #064d86;
  font-size: 1.2em;
  font-weight: 700;
  padding: 0.5rem 0;
}
.form__input {
  border: none;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0px 2px 5px 0.3px rgba(7, 41, 75, 0.25);
}
.form__input:focus {
  border: 0.2rem solid #7cc7fd;
  outline: none;
}
.form__option {
  text-transform: capitalize;
}
.form__container__search {
  display: flex;
  flex-direction: column-reverse;
  gap: 2rem;
}
.form__container__search button {
  flex: 0.5;
}
.form__container__search input {
  flex: 4;
}
@media (min-width: 768px) {
  .form__row {
    flex-direction: row;
  }
  .form__row__item {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .form__container__search {
    flex-direction: row-reverse;
  }
}